body {
  overflow-x: hidden;
}

li {
  list-style: none;
  padding: 0;
}

.wrap {
  margin: 0 auto;
  width: 98%;
  max-width: 1140px;
  margin-top: 50px;
}

.main {
  text-align: center;
  border-radius: 15px;
  background: linear-gradient(
    to bottom left,
    rgb(70, 131, 228),
    20%,
    rgb(166, 200, 235)
  );
}

.header {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: center;
}

.cities {
  background: linear-gradient(
    to bottom left,
    rgb(70, 131, 228),
    20%,
    rgb(166, 200, 235)
  );
}
